<template>
  <div class="json">
    <div class="left">
      <div class="number">
        <span>需要新增的参数</span>
        <el-input v-model="parameter" placeholder="新增参数"></el-input>
        <el-button type="success" @click="InsertData">插入json</el-button>
      </div>
      <el-input type="textarea" :rows="10" placeholder="添加JSON，检查，新增" v-model="textarea"></el-input>
      <el-button class="submit" type="success" @click="saveHtml">渲染</el-button>
      <figure v-show="showFigureImg" class="show-img">
        <img :src="figureImg" alt />
      </figure>
    </div>
    <div class="right">
      <i class="right-box">{{listArr.length}}</i>
      <div class="add-img">
        <span>添加图片</span>
        <el-input v-model="parameterImg" placeholder="插入图片的参数名称，可不填"></el-input>
        <el-button type="success" @click="addParameter">添加</el-button>
      </div>
      <el-input style="margin-bottom:20px;" v-model="contrast" placeholder="上传图片的名称所对应的参数名"></el-input>
      <!-- 上传图片 -->
      <qiniuView ref="Qiuniu"></qiniuView>
      <ul>
        <li
          v-for="(obj,i) in listArr"
          :key="i"
          :class="{drag_enter: isDragenter == i ? true : false}"
          @dragenter="isDragenter=i"
          @dragleave="isDragenter=null"
          @dragover.prevent
          @drop="$refs.Qiuniu.ondragdrop($event,i)"
        >
          <div>
            <p v-for="(v,k,c) in obj" :key="c" v-if="v && v.trim() != ''">
              <em>{{k}}</em>
              {{v}}
            </p>
          </div>
          <figure
            v-if="parameterImg && obj[parameterImg]"
            @mouseover="showImage(i)"
            @mouseout="hidenImage(i)"
          >
            <img :src="obj[parameterImg]" alt />
          </figure>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import qiniuView from "./qiniu";
export default {
  components: { qiniuView },
  data() {
    return {
      contrast:'',
      parameterImg: "",
      parameter: "",
      textarea: "",
      listArr: [],
      upimgView: false,
      showFigureImg: false,
      figureImg: "",
      isDragenter: null
    };
  },
  methods: {
    upDataList(){
      this.textarea = JSON.stringify(this.listArr);
    },
    // json 展示
    saveHtml() {
      this.listArr = JSON.parse(this.textarea);
    },
    // 插入数据
    InsertData() {
      if (this.parameter) {
        let arr = JSON.parse(this.textarea);
        arr.forEach(e => {
          e[this.parameter] = "";
        });
        this.textarea = JSON.stringify(arr);
      }
    },
    showImage(i) {
      this.showFigureImg = true;
      this.figureImg = this.listArr[i][this.parameterImg];
    },
    hidenImage(i) {
      this.showFigureImg = false;
    },
    // 添加图片
    addParameter() {
      if (this.$refs.Qiuniu.tasks.length > 0 && this.parameterImg != "") {
        let arr = [];
        let imgs = this.$refs.Qiuniu.tasks
        if(this.contrast){
          let obj = {}
          imgs.forEach(e => {
            obj[e.name.trim()] = e.url + "?imageslim"
          });
          this.listArr.forEach((item, index) => {
            let name = item.name.trim()
            if (obj[name]) {
              item[this.parameterImg] = obj[name]
            }
            arr.push(item);
          });
        }else{
          this.listArr.forEach((item, index) => {
            if (imgs[index]) {
              item[this.parameterImg] =
                imgs[index].url + "?imageslim";
            }
            arr.push(item);
          });
        }
        
        this.$set(this.listArr, arr);
        this.upDataList()
      }
    }
  }
};
</script>

<style lang="less">
.json {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  .number,
  .add-img {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 20px;
    .el-input {
      max-width: 200px;
      margin: 0 30px;
    }
  }
  .left {
    flex: 1;
    min-width: 500px;
    height: calc(~"100vh - 50px");
    margin-right: 30px;
    padding: 10px;
    .show-img {
      position: fixed;
      left: 20px;
      top: 50%;
      box-shadow: 0 0 10px 1px #eee;
      transform: translate3d(0, -50%, 0);
      background: rgba(255,255,255,.9);
      padding: 20px;
    }
    textarea {
      height: calc(~"100vh - 200px");
    }
    .submit {
      display: block;
      margin: 30px auto 0;
    }
  }
  .right {
    flex: 1;
    min-width: 800px;
    padding: 10px;
    background: #08f5e470;
    max-height: calc(~"100vh - 50px");
    overflow: auto;
    position: relative;
    .right-box {
      padding: 0 7px;
      line-height: 30px;
      font-size: 14px;
      background: rgba(0, 0, 0, 0.8);
      color: #fff;
      position: fixed;
      right: 0;
      top: 60px;
      font-style: normal;
      border-radius: 2px;
    }
    ul {
      li {
        margin-bottom: 20px;
        background: #f5f5f5;
        padding: 10px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        position: relative;
        &.drag_enter {
          color: #000;
          background-color: rgb(156, 197, 239);
          &:after {
            content: "松开鼠标，开始上传。";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgb(156, 197, 239);
            z-index: 10;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 20px;
          }
        }
        figure {
          display:flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          flex-shrink: 0;
          width: 200px;
          height: 100%;
          overflow: hidden;
          vertical-align: middle;
          margin-left: 20px;
          img {
            display:block;
            width: 100%;
          }
        }
        p {
          line-height: 20px;
          font-size: 12px;
          padding-left: 80px;
          position: relative;
          border-bottom: 1px solid #eee;
          margin-bottom: 5px;
          min-height: 20px;
          em {
            position: absolute;
            left: 0px;
            top: 0px;
            line-height: 20px;
            width: 70px;
            font-weight: bold;
            font-style: normal;
          }
        }
      }
    }
  }
}
</style>



