import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import shopView from '@/components/shop'
import jsonImg from '@/components/jsonImg'
import animationView from '@/components/animation'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'shopView',
      component: shopView
    },
    {
      path: '/time',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/jsonimg',
      name: 'jsonImg',
      component: jsonImg
    },
    {
      path: '/animation',
      name: 'animation',
      component: animationView
    }
  ]
})


