<template>
  <div class="header">
    <ul>
      <li v-for="(item,i) in routers" :key="i" :class="routername == item.url ? 'selected' : ''">
        <router-link :to="{ name: item.url}">{{item.name}}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  mounted() {
    this.routername = this.$router.history.current.name;
  },
  methods: {
    fetchData() {
      console.log(this.$router.history.current.name);
      this.routername = this.$router.history.current.name;
    }
  },
  watch: {
    $route: "fetchData"
  },
  data() {
    return {
      routername: "shopView",
      routers: [
        {
          url: "shopView",
          name: "word文档转json"
        },
        {
          url: "HelloWorld",
          name: "随机时间生成器"
        },
        {
          url: "jsonImg",
          name: "JSON添加参数"
        },
        {
          url: "animation",
          name: "animation动画"
        }
      ]
    };
  }
};
</script>

<style lang="less">
.header {
  width: 100%;
  height: 50px;
  background: #409eff;
  margin-bottom: 10px;
  ul {
    display: flex;
    flex-direction: row;

    li {
      position: relative;
      &.selected {
        background: #909399;
      }
      a {
        text-decoration: none;
        display: block;
        padding: 0 20px;
        line-height: 50px;
        font-size: 14px;
        color: #fff;
      }
    }
  }
}
</style>



