<template>
  <div class="edit_container">
    <div class="left">
      <div class="number">
        <span>需要的参数数量</span>
        <el-input v-model="num" placeholder="需要的参数数量：3"></el-input>
        <el-button type="success" @click="showInputs = true">生成参数框</el-button>
      </div>
      <div class="inputs" v-if="showInputs">
        <label v-for="i in Number(num)" :key="i">
          <el-input v-model="valus[i-1]" placeholder="参数名"></el-input>
        </label>
      </div>
      <quill-editor
        v-model="content"
        ref="myQuillEditor"
        :options="editorOption"
        @blur="onEditorBlur($event)"
        @focus="onEditorFocus($event)"
        @change="onEditorChange($event)"
      ></quill-editor>
      <el-button class="submit" type="success" @click="saveHtml">生成JSON</el-button>
      <p class="hint">
        1、这是一个将Word文档转化为json的工具
        <br />2、输入需要的参数数量，点击生成会出现你需要参数量的参数输入框
        <br />3、很重要的一点，参数的输入顺序必须同你的文档顺序相同
        <br />4、没有参数的可以用空格代替
        <br />
        <span>*多个组之间通过多个换行符区分，最少2个换行符</span>
      </p>
    </div>
    <div class="right">
      <el-input type="textarea" :rows="10" placeholder="返回的JSON" v-model="textarea"></el-input>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      content: `<p>hello world</p>`,
      editorOption: {},
      num: "",
      showInputs: false,
      valus: [],
      textarea: "",
      cc: {
        name: "亚瑟格兰名品购物村",
        content:
          "在亚瑟格兰名品奥特莱斯可享低至三折超值优惠！其下的5家奥特莱斯紧邻意大利的主要城市，在此能找到众多热门大牌，并享受3至7折的超值优惠，带来最佳购物体验。",
        offer:
          "即日起至 2020-06-30出示电子邀请函（可从银联国际官网下载）与您的银联卡，即可换领一张九折卡。",
        address: `塞拉瓦莱店（近米兰）：Via della Moda1，15069 Serravalle Scrivia (AL)，Italy<br>
                诺文塔购店（近威尼斯）：Via Marco Polo 1，30020 Noventa di Piave (VE)，Italy<br>
                巴贝里诺店（近博洛尼亚）: Via Meucci snc, 50031 Barberino di Mugello， Italy<br>
                罗马诺城堡店（近罗马）: Via Ponte di Piscina Cupa 64 00128 Castel Romano Rome, Italy<br>
                拉瑞吉亚店（近佛罗伦萨）: S.P. 336 Sannitica 81025 Marcianise , Italy`,
        url: "http://www.enlavaguada.com/W/do/centre/inicio（官方外网）"
      }
    };
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill;
    }
  },
  methods: {
    onEditorReady(editor) {
      // 准备编辑器
    },
    onEditorBlur() {}, // 失去焦点事件
    onEditorFocus() {}, // 获得焦点事件
    onEditorChange(e) {
      console.log(e);

      this.text = e.text;
    }, // 内容改变事件
    saveHtml(event) {
      // 1、分成每个单独的段落
      let text = this.text.replace(/\n\n\n\n\n/g, "<<<<<<>>>>>>>");
      text = this.text.replace(/\n\n\n\n/g, "<<<<<<>>>>>>>");
      text = text.replace(/\n\n\n/g, "<<<<<<>>>>>>>");
      text = text.replace(/\n/g, "thisIsFengGeFu");
      console.log(text);

      let arr = text.split("<<<<<<>>>>>>>");
      console.log(arr);

      // 2、每个单独段落拆分
      let arr2 = [];
      arr.forEach(e => {
        let o = {};

        let me = e.split("thisIsFengGeFu");
        console.log(me);

        this.valus.forEach((c, i) => {
          o[c] = me[i];
        });
        arr2.push(o);
      });
      // 3、赋值
      this.textarea = JSON.stringify(arr2);
    }
  }
};
</script>

<style lang="less">
.ql-editor {
  min-height: 400px;
  max-height: 100vh;
  overflow: auto;
}
.edit_container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  .left {
    flex: 1;
    min-width: 800px;
    .inputs {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      label {
        padding: 0 10px;
        margin-bottom: 10px;
      }
      .el-input {
        width: 100px;
      }
    }
    .hint {
      font-size: 12px;
      line-height: 20px;
      color: #666;
      span {
        color: #f60;
      }
    }
  }
  .right {
    flex: 1;
    min-width: 300px;
    padding: 10px;
    background: #08f5e470;
    margin-left: 30px;
    .el-input {
      width: 100%;
    }
    textarea {
      height: 100vh;
    }
  }
  .submit {
    display: block;
    margin: 30px auto;
  }
}
.number {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 20px;
  .el-input {
    max-width: 200px;
    margin: 0 30px;
  }
}
</style>


