<template>
  <div class="qiniu">
    <div class="btns">
      <el-button type="primary">
        <a
          class="btn-success"
          href="https://tinypng.com/"
          target="_blank"
          style="color:#fff;"
        >在线压缩图片（TinyPNG）</a>
      </el-button>
      <el-button
        type="success"
        id="drag_btn"
        class="btn-primary"
        @click="$refs.uploadFile.click()"
      >点击上传图片</el-button>
    </div>
    <p class="tip text-danger">
      建议将图片压缩后，再上传到七牛，支持jpg/png；上传的图片顺序将按照json顺序插入
      <i
        class="el-icon-info"
        @click="imgsList = !imgsList"
      >
        <span>点击查看上传图片列表</span>
      </i>
    </p>
    <input ref="uploadFile" multiple type="file" @change="onFileChange" value="上传" accept="*" />
    <div
      id="drag"
      class="drag"
      :class="{drag_enter: isDragenter}"
      @dragenter="isDragenter=true"
      @dragleave="isDragenter=false"
      @dragover.prevent
      @drop="ondragdrop($event)"
    >
      <em>图片上传数量：{{tasks.length}}</em>
    </div>

    <!-- 图片列表 -->
    <transition name="fade">
      <div class="img-list-view" v-show="imgsList">
        <i class="mask" @click="imgsList = !imgsList"></i>
        <em class="el-icon-error" @click="imgsList = !imgsList"></em>
        <div id="upload_files" class="upload_files">
          <h3>
            上传文件列表
            <button class="btn btn-sm btn-primary" @click="cleanList">清空列表</button>
            <span class="text-danger">清空功能会清空localStorage中的缓存数据</span>
          </h3>
          <div class="changeback">
            <button class="btn btn-sm btn-default" @click="changeback = !changeback">更换背景色</button>
          </div>

          <table :style="changeback ? 'background: #999' : ''">
            <thead>
              <tr>
                <th style="width: 80px">序号</th>
                <th style="width: 280px">预览</th>
                <th style="width: auto">地址</th>
                <th style="width: 70px;">&nbsp;</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(t,index) in tasks" :key="index">
                <td>{{index+1}}</td>
                <td v-if="t.process!=100">loading ...</td>
                <td v-else>
                  <a :href="getShowUrl(t)" target="_blank">
                    <div
                      v-if="t.isImg"
                      class="preview"
                      :style="{backgroundImage:'url('+ getShowUrl(t) +')'}"
                    ></div>
                    <span v-else>{{t.type}} 不支持预览，点击新窗口查看</span>
                  </a>
                </td>

                <td v-if="t.process!=100" class="top">
                  <div class="process">
                    <div :style="{width:t.process+'%'}">{{t.process}}%</div>
                  </div>
                </td>
                <td v-else class="top">
                  <div class="urls">
                    <div v-if="t.isImg">
                      <button
                        class="btn btn-sm btn-default"
                        :class="{'btn-info':t.showType==1}"
                        @click="t.showType=1"
                      >瘦身</button>&nbsp;
                      <button
                        class="btn btn-sm btn-default"
                        :class="{'btn-info':t.showType==2}"
                        @click="t.showType=2"
                      >模糊</button>&nbsp;
                      <button
                        class="btn btn-sm btn-default"
                        :class="{'btn-info':t.showType==3}"
                        @click="t.showType=3"
                      >原图</button>&nbsp;
                      <a
                        class="btn btn-link"
                        href="//developer.qiniu.com/dora/manual/3683/img-directions-for-use"
                        target="_blank"
                      >
                        <span>更多特效</span>
                      </a>
                    </div>
                    <br />
                    <div class="form-group">
                      <div class="input-group">
                        <input
                          type="text"
                          class="form-control"
                          v-dom="t"
                          readonly
                          :value="getShowUrl(t)"
                          @copy="beforeCopy(t)"
                        />
                        <div class="input-group-addon">
                          <a href="javascript:;" @click="copy(t)">复制</a>
                        </div>
                      </div>
                    </div>
                    <div class="width-height" v-if="t.image">
                      宽高：{{t.width}} * {{t.height}}
                      <br />
                      原图大小：{{(t.size/1000).toFixed(2)}}k
                      <br />
                      类型：{{t.format}}
                    </div>
                  </div>
                </td>
                <td>
                  <a class="btn btn-link" href="javascript:;" @click="removeTask(t.id)">删除</a>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </transition>
  </div>
</template>
