  margin-right: 10px;
  background-color: rgb(253, 167, 88);
  color: rgb(255, 255, 255);
  padding: 2px 10px;
  border-radius: 6px;
}

.qiniu .tip {
  margin: 10px 0 0 0;
  font-size: 12px;
  color: rgb(153, 153, 153);
  font-size: 14px;
  line-height: 30px;
  position: relative;
  i {
    position: absolute;
    left: 0;
    top: 0;
    width: 30px;
    height: 30px;
    font-size: 24px;
    color: #f56c6c;
    cursor: pointer;
    &:hover {
      span {
        display: block;
      }
    }
    span {
      display: none;
      position: absolute;
      left: 40px;
      top: -5px;
      width: 150px;
      padding: 0 10px;
