</style>
<script>
import Vue from "vue";
import { ajax, jsonp } from "../utils/utils.js";

let qiniu;
if (!Vue.prototype.$isServer) {
  qiniu = require("qiniu-js");
}

const ALLOW_TYPES = {
  "image/jpg": { type: "jpg", isImg: true },
  "image/jpeg": { type: "jpeg", isImg: true },
  "image/png": { type: "png", isImg: true },
  "image/gif": { type: "gif", isImg: true },
  "audio/mp3": { type: "mp3", isImg: false },
  "video/mp4": { type: "mp4", isImg: false },
  "image/svg+xml": { type: "svg", isImg: true },
  "application/javascript": { type: "js", isImg: false },
  "text/css": { type: "css", isImg: false }
};

let storage = [],
  localStorage;

export default {
  name: "Qiuniu",
  data() {
    return {
      changeback: false,
      isDragenter: false,
      ALLOW_TYPES,
      token: "",
      // tasks:[
      //   { id:'5', process:100, showType:1, url:'https://fes.qyerstatic.com/Fp6seisE2ivbe3Q_BoO4oIUmBdhI',isImg:true,type:'jpg',width:1,height:1,size:1 },
      // ]
      tasks: storage,
      imgsList: false,
      tasksIndex: null
    };
  },
  computed: {
    accept() {
      return Object.keys(this.ALLOW_TYPES)
        .map(type => type)
        .join(",");
    }
  },
  async mounted() {
    localStorage = window.localStorage;
    storage = JSON.parse(localStorage.getItem("mock_server_upload")) || [];
    this.tasks = storage;

    // const token = await this.getQiniuToken();
    // this.token = token;
  },
  directives: {
    dom: {
      bind: function(el, binding) {
        let obj = binding.value;
        Vue.set(obj, "el", el);
      }
    }
  },
  methods: {
    gotoPosition(n) {
      window.scrollTo(0, n);
    },
    gotoFooter() {
      this.gotoPosition(document.body.scrollHeight);
    },
    getShowUrl(t) {
      // 更新获取地址不带协议
      if (!t.isImg) {
        return t.url.replace(/http:\/\/|https:\/\//, "//");
      }

      let url = t.url.replace(/http:\/\/|https:\/\//, "//");
      if (t.showType == 1) {
        url += "?imageslim";
      } else if (t.showType == 2) {
        // 图片模糊地址
        url += "?imageMogr2/thumbnail/350x/blur/30x15";
      } else if (t.showType == 3) {
        // 原图地址
      }
      return url;
    },
    // 拖拽上传
    ondragdrop(e, i) {
      e.preventDefault();
      this.isDragenter = false;
      this.$parent.isDragenter = null;
      let files = [];
      // 拖拽的文件会放到 e.dataTransfer.files 里面
      files = e.dataTransfer.files;
      // chrome 下 e.dataTransfer.files 总是0，需要特殊处理
      if (files.length === 0) {
        // 用forEach函数遍历可以成功获取文件
        [].forEach.call(
          e.dataTransfer.files,
          function(file) {
            files.push(file);
          },
          false
        );
      }
      if (i) {
        this.tasksIndex = i;
      } else {
        this.tasksIndex = null;
      }
      this.getQiniuToken(0, files);
    },

    onFileChange(e) {
      // Array.from(e.target.files).forEach(file => this.qiniuUplad(file));
      this.getQiniuToken(1, e.target.files);
    },

    getFileKey(size) {
      return (
        Date.now().toString(36) +
        size.toString(36) +
        "_" +
        Math.random()
          .toString(36)
          .substr(2) +
        Math.random()
          .toString(36)
          .substr(2)
      );
    },

    async qiniuUplad(file) {
      const fileKey = this.getFileKey(file.size);
      const _this = this;

      const config = {
        useCdnDomain: true
      };
      const putExtra = {
        fname: fileKey,
        params: {},
        mimeType: null
      };

      const observer = {
        next(res) {
          _this.onUploadProgress(fileKey, res.total.percent);
        },
        error(err) {
          console.log("error");
          console.error(err);
        },
        complete(res) {
          _this.onFileUploaded(fileKey, res.key);
        }
      };
      // 开始上传
      // file.id = fileKey

      this.onFilesAdded(file, fileKey);
      let observable = qiniu.upload(file, null, this.token, putExtra, config);
      let subscription = observable.subscribe(observer);
    },
    getQiniuToken(n, files) {
      ajax({
        url: "//openapi.qyer.ad/interutils/generateToken",
        method: "GET",
        filterErrorCode: false
      }).then(res => {
        if (res.status == 0) {
          this.token = res.data;
          console.log(res);

          if (n) {
            Array.from(files).forEach(file => this.qiniuUplad(file));
          } else {
            Array.from(files).forEach(
              file => this.ALLOW_TYPES[file.type] && this.qiniuUplad(file)
            );
          }
        }
      });
    },

    cleanList() {
      this.tasks = [];
      localStorage.removeItem("mock_server_upload");
    },

    beforeCopy(task) {
      if (task.showType !== 1) {
        window.alert("请注意，复制的路径不是压缩地址!");
      }
    },

    copy(task) {
      task.el.select();
      window.document.execCommand("Copy");
      this.$message({
        message: "复制成功。",
        type: "success"
      });
    },

    removeTask(id) {
      let index = this.tasks.findIndex(t => t.id === id);
      this.tasks.splice(index, 1);
      localStorage.setItem("mock_server_upload", JSON.stringify(this.tasks));
    },

    onFilesAdded(file, key) {
      console.log(file, key);
      
      let conf = ALLOW_TYPES[file.type];
      let isImg = conf ? conf.isImg : false;
      this.tasks.push({
        id: key,
        process: 2,
        showType: 1,
        url: "",
        isImg,
        type: file.type,
        width: 0,
        height: 0,
        image: 0,
        name: file.name.substring(0,file.name.indexOf('.'))
      });

      this.isDragenter = false;
    },

    onRemoveFile(file) {
      this.isDragenter = false;
    },
    onUploadProgress(fileKey, percent) {
      let p = Math.min(99, percent);
      let t = this.tasks.find(t => t.id === fileKey);
      t.process = p;
    },
    onFileUploaded(fileKey, key) {
      let t = this.tasks.find(t => t.id === fileKey);
      t.process = 100;
      t.url = `http://picb2b.qyer.com/${key}`;

      if (this.tasksIndex != null) {
        let obj = this.$parent.listArr[this.tasksIndex];
        obj[this.$parent.parameterImg] = t.url;
        this.$set(this.$parent.listArr[this.tasksIndex], obj);
        this.$parent.upDataList()

        this.tasksIndex = null;
      }

      ajax({
        url: `http://picb2b.qyer.com/${key}?imageInfo`,
        filterErrorCode: false,
        method: "GET"
      }).then(data => {
        t.width = data.width;
        t.height = data.height;
        t.size = data.size;
        t.format = data.format;
        t.image = 1;
        localStorage.setItem("mock_server_upload", JSON.stringify(this.tasks));
        // 添加
      });
    }
  }
};
</script>



